.delivery {
  background-color: $white;
  padding: 91px 0 50px;

  &__inner {
    display: flex;
  }

  &__content {
    max-width: 530px;
    margin-right: 33px;
  }
}

.delivery-content {
  &__title {
    font-size: 36px;
    line-height: 50px;
    margin-bottom: 13px;
    text-align: left;
    &::before {
      left: 0;
      transform: translateX(0%);
    }
  }

  &__text {
    font-size: 16px;
    line-height: 20px;
    color: rgba(34, 34, 34, 0.7);
    margin-bottom: 25px;
  }
}

.russian-map {
  svg path {
    transition: all 0.3s ease-in-out;
    &:hover {
      cursor: pointer;
    }
  }

  &__tool {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 1px;
    padding: 5px 10px;
    background-color: #ccc;
    font-size: 10px;
    line-height: 14px;
    color: $default;

    &::before {
      position: absolute;
      content: "";
      bottom: -20px;
      left: 10px;
      width: 9px;
      height: 10px;
      border: 10px solid transparent;
      border-top-color: #ccc;
      pointer-events: none;
    }
    &--active {
      display: block;
    }
  }
}
