.post {
  padding-bottom: 50px;

  &__title {
    margin-bottom: 35px;
  }

  &__content {
    margin-bottom: 21px;
    font-size: 16px;
    line-height: 30px;
    color: $default
  }

  &__box {
    display: flex;
    margin-bottom: 10px;
  }

  &__img {
    margin-right: 30px;
    width: 645px;
    max-width: inherit;
    height: 300px;
  }

  &__text-subtitle {
    font-weight: 700;
    line-height: 20px;
  }

  &__bottom {
      @include flex;
      justify-content: space-between;
      align-items: center;
  }
  &__link {
    
  }
  &__social {
    @include flex-v-center;
  }
  &__social-text {
    display: block;
    font-size: 16px;
    line-height: 30px;
    color: $default
  }
} 



.social {
  @include flex-v-center;
  &__item {
    margin-left: 20px;
  }

  &__link {
    display: block;
    color: $default;
    transition: color .2s ease-in-out;
    svg {
      width: 100%;
      height: 100%;
      fill: transparent;
      stroke: currentColor;
    }

    &:hover {
      color: $accent
    }
  }
 
  &__link--fb {
    width: 20px;
    height: 20px;
  }
  &__link--tg {
    width: 20px;
    height: 20px;
  }
  &__link--inst {
    width: 20px;
    height: 20px;
  }
  &__link--tw {
    width: 24px;
    height: 20px;
  }
  &__link--odn {
    width: 14px;
    height: 20px;
  }
  &__link--vk {
    width: 30px;
    height: 20px;
  }

  
}
