.benefits {
  padding: 60px 0 60px;
  &__title {
    margin-bottom: 62px;
  }
}

.benefits-list {
  &__item {
    grid-column: 3 span;
    padding-top: 86px;
    text-align: center;
  }
  &__title {
    font-weight: 700;
    font-size: 36px;
    line-height: 49px;
    color: $default;
    margin-bottom: 13px;
  }
  &__txt {
    position: relative;
    display: block;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    color: #222222;
    padding-bottom: 24px;
    margin-bottom: 12px;

    &::before {
      position: absolute;
      content: "";
      width: 50px;
      height: 5px;
      background-color: $accent;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  &__descr {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
  }
  &__item--work {
    background-image: url('../../img/benef-1.png');
    background-position: top center;
    background-repeat: no-repeat;
    background-size: auto;
  }
  &__item--delivery {
    background-image: url('../../img/benef-2.png');
    background-repeat: no-repeat;
    background-position: top center;
    background-size: auto;
  }
  &__item--best {
    background-image: url('../img/benef-3.png');
    background-position: top center;
    background-repeat: no-repeat;
    background-size: auto;
  }
  &__item--goal {
    background-image: url('../img/benef-4.png');
    background-position: top center;
    background-repeat: no-repeat;
    background-size: auto;
  }
}
