.vacancies {
  &__title {
    margin-bottom: 30px;
  }

  &__descr {
    max-width: 983px;
    margin-bottom: 45px;
    font-size: 14px;
    line-height: 20px;
    color: $default;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 30px;
    row-gap: 30px;
    margin-bottom: 50px;
    
  }

  &__item {
    grid-column: 6 span;
  }

  &__form {
    margin-bottom: 50px;
  }
}
.open {
  .vacancies-item__btn {
    svg {
      transform: rotate(90deg);
    }
  }
  .vacancies-content {
    opacity: 1;
    padding: 30px 20px 30px;
  }
}

.vacancies-item {

  &__top {
    @include flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
    border-radius: 1px;
    padding: 15px 20px;
    color: $default;
    margin-bottom: 10px;
  }

  &__city {
    font-weight: 700;
    font-size: 18px;
    line-height: 20px;
  }

  &__prof {
    font-size: 14px;
    line-height: 19px;
  }

  &__btn {
    @include flex-v-center;
    color: $default;
    transition: color .2s ease-in-out;

    svg {
      display: block;
      margin-left: 10px;
      width: 5px;
      height: 10px;
      fill: transparent;
      stroke: currentColor;
      transition: transform .2s ease-in-out;
    }

    &:hover {
      color: $accent;
    }
  }

}

.vacancies-content {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  padding: 0 20px 0;
  will-change: max-height;
  transition: opacity .2s ease-in-out, overflow .2s ease-in-out, max-height .2s ease-in-out, padding .2s ease-in-out;

  background-color: $gray;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
  border-radius: 1px;
  color: $default;
  box-sizing: content-box;
  &__city {
    padding-right: 30px;
  }

  &__top {
    margin-bottom: 32px;
  }

  &__item {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 25px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
  }

  &__list-item {
    padding-left: 20px;
    font-size: 12px;
    line-height: 20px;
  }

  &__link {
    box-sizing: border-box;
  }
}