.nav {

  &__list {
    @include flex-v-center;
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-right: 30px;
    }
  }

  &__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color: $default;
    transition: color .2s ease-in-out;

    &--drop-active {
      color: $accent;

      &+.menu-dropdown {
        opacity: 1;
        visibility: visible;
      }
    }

    &-arr {
      padding-left: 8px;
      pointer-events: none;

      svg {
        width: 11px;
        height: 9px;
        fill: currentColor;
        pointer-events: none;
      }
    }

    &:focus {
      outline: 1px solid $accent;
      outline-offset: 2px;
    }

    &:hover {
      color: $accent;
    }

    &:active {
      color: $accent;
    }

  }
}

.menu-dropdown {
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  width: 250px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
  border-radius: 0px 0px 1px 1px;
  z-index: 3;

  opacity: 0;
  visibility: hidden;

  &--active {
    opacity: 1;
    visibility: visible;
  }

  &__item {
    &:not(:last-child) {
      border-bottom: 1px solid $gray;
    }
  }

  &__link {
    display: block;
    width: 100%;
    padding-top: 3px;
    padding-left: 15px;
    padding-bottom: 5px;
    padding-right: 10px;
    font-size: 16px;
    line-height: 22px;
    color: #000000;
    background-color: $white;
    transition: color .2s ease-in-out, background-color .2s ease-in-out;

    &:hover {
      background-color: $accent;
      color: $white;
    }
  }
}