.pagination {

  &__list {
    @include flex-all-center;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 30px;
    }
  }

  &__link {
    @include flex-all-center;
    min-width: 30px;
    height: 30px;
    padding: 2px 4px;
    font-weight: 700;
    line-height: 25px;
    font-size: 18px;
    border: 1px solid $default;
    color: $default;
    transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
    &--active {
      color: $white;
      background-color: $accent;
      border-color: $accent;
    }

    &:focus {
      outline: 1px solid $accent;
      outline-offset: 2px;
    }
  
    &:hover {
      background-color: $accent;
      color: $white;
      border-color: $accent;
    }
  
    &:active {
      background-color: $accent;
      color: $white;
      border-color: $accent;
    }
  }

  &__prev {
    display: block;
    margin-right: 50px;
    color: $default;
    transition: color .2s ease-in-out;

    &:focus {
      outline: 1px solid $accent;
      outline-offset: 2px;
    }
  
    &:hover {
      color: $accent;
    }
  
    &:active {
      color: $accent;
    }

    svg {
      width: 10px;
      height: 20px;
      fill: transparent;
      stroke: currentColor;
    }
  }

  &__next {
    display: block;
    margin-left: 50px;
    color: $default;
    transition: color .2s ease-in-out;

    &:focus {
      outline: 1px solid $accent;
      outline-offset: 2px;
    }
  
    &:hover {
      color: $accent;
    }
  
    &:active {
      color: $accent;
    }
    svg {
      width: 10px;
      height: 20px;
      fill: transparent;
      stroke: currentColor;
    }
  }
}