body,
html {
  height: 100%;
}

.page {
  height: 100%;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  position: relative;
  font-family: "Noto Sans";
  font-weight: 400;
  font-style: normal;
  background-color: $white;
  color: rgba(34, 34, 34, 0.7);
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

//,  ,       
.hidden {
  overflow: hidden;
}

.wrapper {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 100%;
}

.main {
  flex-grow: 1;
}

.container {
  max-width: 1350px;
  padding: 0 15px;
  margin: 0 auto;
}

.title {
  position: relative;
  font-family: "Noto Sans";
  font-weight: 700;
  font-size: 50px;
  line-height: 68px;
  color: $default;
  text-align: center;

  &::before {
    content: "";
    position: absolute;
    top: -20px;
    left: 50%;
    transform: translateX(-50%);
    width: 150px;
    height: 5px;
    background-color: $accent;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.list-reset {
  list-style: none;
  padding: 0;
  margin: 0;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: transparent;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }

}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;

  &__item {
    grid-column: 4 span;
  }
}

.btn-fill {
  display: inline-block;

  background-color: $accent;
  color: $white;
  padding: 7px 20px;
  min-height: 40px;
  font-size: 18px;
  line-height: 25px;
  transition: background-color 0.2s ease-in-out;

  &:focus {
    outline: 1px solid $accent;
    outline-offset: 2px;
  }

  &:hover {
    background-color: $accent-hover;
  }

  &:active {
    background-color: $accent-hover;
  }
}

.title-page {
  font-weight: 700;
  font-size: 50px;
  line-height: 68px;
  color: $default;
}

.accordion {
  max-height: 0;
  overflow: hidden;
  will-change: max-height;
  transition: opacity 0.2s ease-in-out, overflow 0.2s ease-in-out,
    max-height 0.2s ease-in-out, padding 0.2s ease-in-out;
}