.tabs {
  &__nav-btn {
    display: inline-block;
    padding: 9px 20px;
    font-size: 18px;
    line-height: 20px;
    color: $default;

    // transition: color .1s ease-in-out, background-color .1s ease-in-out;
    &--active {
      color: $white;
      background-color: $accent;
    }

    &:focus {
      background-color: $accent;
    }
  }

  &__content {
    padding: 20px 30px 20px;
    background-color: $gray;
    border-radius: 1px;
  }

  &__panel {}

  &__text {
    font-size: 16px;
    line-height: 30px;
    color: $default;

    span {
      font-weight: 700;
    }
  }

  &__subtitle {
    font-weight: 700;
    font-size: 16px;
    line-height: 30px;
    color: $default;
  }

  &__download {
    background-color: $gray;
  }

  &__download-link {
    display: block;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color: $default;
    transition: color .2s ease-in-out;

    &:focus {
      outline: 1px solid $accent;
      outline-offset: 2px;
    }

    &:hover {
      color: $accent;
    }

    &:active {
      color: $accent;
    }
  }

  &__download-descr {
    padding-left: 50px;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: currentColor;

    svg {
      width: 20px;
      height: 20px;
      object-fit: cover;
      stroke: currentColor;
    }
  }

  &__table {
    border-collapse: collapse;
    width: 100%;

    th, td {
      border: 1px solid $white;
    }

    td {
      color: $default ;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      text-align: center;
      padding: 4px;
      &:first-child{
        text-align: left;
        padding-left: 20px;
      }
    }
    
  }

  &__title {
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color: $default;
    text-align: left;
    padding: 4px;
    padding-left: 20px;
  }
  
}