.form-input {
  display: inline-block;
  padding: 10px 15px;
  border: 1px solid $text-color;
  border-radius: 1px;
  color: $default;
  &::placeholder {
    color: $text-color;
  }

  &:focus {
    outline: 1px solid $accent;
    outline-offset: 2px;
  }
}

.form-searh {
  position: relative;
  height: 22px;
  &__input {
    position: absolute;
    right: 0;
    bottom: 0;
    margin-left: auto;
    width: 74px;
    color: $default;
    padding-left: 26px;
    background-image: url("../img/search.svg");
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 16px 16px;
    transition: width 0.3s ease-in-out;
    caret-color: #b2b8bb;
    &::placeholder {
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      color: #b2b8bb;
    }
    &--active {
      width: 380px;
      outline: none;
      + .form-searh__reset {
        opacity: 1;
        visibility: visible;
      }
    }

    &:focus {
      outline: 1px solid $accent;
      outline-offset: 2px;
    }
  }
  &__reset {
    position: absolute;
    right: 0;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    svg {
      width: 12px;
      height: 12px;
      stroke: $default;
      vertical-align: middle;
      pointer-events: none;
    }
  }
}


.form {
  max-width: 645px;
  width: 100%;
  background-color: $gray;
  border-radius: 1px;
  padding: 20px 20px 30px;

  &__box {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__label {
    position: relative;
    display: block;
    margin-bottom: 20px;
  }

  &__elem {
    border: 1px solid $text-color;
    padding: 7px 15px;
    color: $default;
    font-size: 12px;
    line-height: 16px;
    transition: border-color .2s ease-in-out;
    &--error {
      border-color: $accent;
      &::placeholder {
        opacity: 0;
      }
    }
    &::placeholder {
      color: $text-color;
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;
    }

    &:focus {
      outline: 1px solid $accent;
      outline-offset: 2px;
      border-color: $default;
    }
  }

  &__input {
    width: 288px;
  }

  &__textarea {
    width: 100%;
    height: 141px;
    resize: none;
  }

  &__error {
    position: absolute;
    display: block;
    left: 15px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 14px;
    line-height: 19px;
    color: $accent;
  }

  &__btn {
    min-height: 40px;
    padding: 7px 20px;
  }
}