.about-page {
  padding-bottom: 50px;

  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 30px;
  }

  &__content {
    grid-column: 9 span;
  }

  &__aside {
    grid-column: 3 span;
  }
}

.about-content {

  &__title {
    margin-bottom: 24px;
  }

  &__info {
    margin-bottom: 34px;

  }

  &__info-title {
    font-weight: 700;
    font-size: 18px;
    line-height: 20px;
    margin-bottom: 6px;
    color: $default;
  }

  &__info-text {
    font-size: 16px;
    line-height: 30px;
    color: $default
  }

  &__info-link {
    text-decoration: underline;
    color: $accent;
    transition: color .3s ease-in-out;

    &:focus {
      outline: 1px solid $accent;
      outline-offset: 2px;
    }

    &:hover {
      color: $accent-hover;
    }

    &:active {
      color: $accent-hover;
    }
  }

  &__link {
    min-width: 210px;
    min-height: 40px;
  }

  &__form {
    max-width: 645px;
    width: 100%;
    margin-bottom: 50px;
  }

  &__form-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    color: $default;
    margin-bottom: 44px;
  }
}