.breadcrumbs {
  padding-top: 17px;
  padding-bottom: 24px;

  &__list {
    @include flex-v-center;
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-right: 10px;

      &::before {
        position: absolute;
        content: "/";
        bottom: 0;
        right: -8px;
        font-size: 13px;
        line-height: 16px;
        color: $text-color;
      }
    }
  }

  &__link {
    font-size: 13px;
    line-height: 16px;
    color: $text-color;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: 1px solid $accent;
      outline-offset: 2px;
    }

    &:hover {
      color: $accent;
    }

    &:active {
      color: $accent;
    }
  }

  &__link--current {
    color: $default;
    &:hover {
      color: $default;
    }
  }
}
