.news {
  padding: 60px 0 70px;
  background-color: $gray;


  &__title {
    margin-bottom: 45px;
  }
}

.news-list {
  grid-template-rows: 300px;
}

.blog-post {
  position: relative;
  overflow: hidden;
  border-radius: 3px;
  height: 100%;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 0.2s ease-in-out;
    background: linear-gradient(0deg, rgba(180, 0, 107, 0.6) 0%, rgba(185, 36, 39, 0.12) 100%);
  }

  &:hover {
    &::before {
      opacity: 1;
    }

    .blog-post__link::after {
      opacity: 0;
    }

    .blog-post__text {
      bottom: 24px;
    }
  }

  &__link {
    display: block;
    height: 100%;
    transition: background 0.3s ease-in-out;

    .image {
      height: 100%;
      width: 100%;
    }

    &::before {
      content: "";
      position: absolute;
      width: 100%;
      top: 0;
      left: 0;
      height: 100%;
      background: linear-gradient(0deg,
          rgba(0, 0, 0, 0.6) 39.58%,
          rgba(0, 0, 0, 0.3) 100%,
          rgba(0, 0, 0, 0.3) 100%);
    }

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 90px;
      transition: opacity 0.2s ease-in-out;
      background: linear-gradient(360deg,
          rgba(0, 0, 0, 0.7) 0%,
          rgba(0, 0, 0, 0) 99.67%);
    }
  }

  &__text {
    position: absolute;
    bottom: -30px;
    left: 20px;
    right: 100px;
    transition: bottom 0.3s ease-in-out;
  }

  &__title {
    font-weight: 700;
    font-size: 28px;
    line-height: 30px;
    margin-bottom: 17px;
    color: $white;

    display: -webkit-box;
    overflow: hidden;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;

    a {
      display: block;
      color: inherit;
    }
  }

  &__read {
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-decoration-line: underline;
    color: $white;
  }
}