.footer {
  background-color: $gray;

  &__inner {
    @include flex-v-center;
    padding: 20px 0;
  }

  &__logo {
    width: 75px;
    height: 50px;
  }

  &__nav {
    margin-left: 50px;
    margin-right: 45px;

    .nav__item {
      &:not(:last-child) {
        margin-right: 45px;
      }
    }

    .nav__link {
      font-weight: 400;
      font-size: 14px;
      line-height: 19px;
      color: $default;
    }
  }

  &__tel {
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    color: $default;
    margin-right: 45px;
    padding-right: 0;

    &::before {
      display: none;
    }
  }

  &__btn-call {
    margin-right: 52px;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    padding: 7px 20px;
    margin-bottom: 0;
  }
}