.home-form {
  padding-top: 90px;
  position: relative;
  &::before {
    position: absolute;
    content: '';
    width: 366px;
    height: 530px;
    bottom: 0;
    right: 65px;
    z-index: 1;
    background-image: url('../img/form-hookah.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
  &__inner {
    position: relative;
    background-image: url("../img/form-bg.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    &::before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(
        90deg,
        #efefef -7.2%,
        rgba(239, 239, 239, 0) 100%
      );
    }

    &::after {
      position: absolute;
      content: "";
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(
        257.1deg,
        #e0e0e0 -19.99%,
        rgba(224, 224, 224, 0) 29.01%
      );
    }
  }
  &__content {
    position: relative;
    max-width: 700px;
    padding: 46px 0 57px;
    z-index: 1;
  }
  &__title {
    font-weight: 700;
    font-size: 50px;
    line-height: 70px;
    color: $default;
    margin-bottom: 15px;
  }
  &__text {
    max-width: 570px;
    margin-bottom: 40px;
    font-size: 22px;
    line-height: 40px;
  }
  
  &__form-box {
    display: flex;
    align-items: flex-start;
  }
  &__form-input {
    max-width: 225px;
    margin-right: 30px;
    margin-bottom: 9px;
  }

  &__form-person {
    font-size: 10px;
    line-height: 14px;
    color: rgba(34, 34, 34, 0.5);
    a {
      transition: color .2s ease-in-out;
      color: $accent;
      opacity: 0.5;

      &:focus {
        outline: 1px solid $accent;
        outline-offset: 2px;
      }
    
      &:hover {
        color: $accent-hover;
      }
    
      &:active {
        color: $accent-hover;
      }
    }
  }
}
