.aside {
  &__catalog {
    margin-bottom: 50px;
  }
  &__info{
    margin-bottom: 20px;
  }
}

.aside-catalog {
  @include flex-all-center;
  flex-direction: column;
  width: 307px;
  padding: 21px 20px 40px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.15);
  border-radius: 1px;
  background-color: #E0E0E0;

  &__title {
    font-weight: 400;
    font-size: 36px;
    line-height: 50px;
    text-align: center;
    color: $default;
    margin-bottom: 39px;
  }

}

.aside-info {
  padding: 28px 30px 36px;
  @include flex-all-center;
  flex-direction: column;
  width: 307px;
  background-color: $accent;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.15);
  border-radius: 1px;

  &__title {
    font-weight: 400;
    font-size: 32px;
    margin-bottom: 25px;
    line-height: 40px;
    text-align: center;
    color: $white;
  }

  &__text {
    font-size: 16px;
    line-height: 22px;
    color: $white;
    margin-bottom: 6px;
    margin-right: auto;
  }


  &__address {
    max-width: 150px;
    margin-bottom: 23px;
  }

  &__contant-link {
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    text-decoration-line: underline;
    color: $white;
  }
}