.cart-images {
  width: 420px;
  grid-column: 4 span;

  &__slider {
    overflow: hidden;
    margin-bottom: 20px;

    .image {
      max-width: 420px;
      max-height: 400px;
      object-fit: cover;
      margin: 0 auto;
    }
  }

  &__nav {
    overflow: hidden;
    width: 282px;
    padding: 0 30px;

    .swiper-slide {
      @include flex-all-center;
      border: 1px solid transparent;
      border-radius: 1px;
      width: 50px;
      height: 50px;
      cursor: pointer;
    }

    .swiper-slide-thumb-active {
      border: 1px solid $default
    }

    .image {
      max-width: 30px;
      max-height: 30px;
      object-fit: cover;
    }

    .cart-images__prev,
    .cart-images__next {
      color: $default;
      transition: color .2s ease-in-out;

      svg {
        width: 12px;
        height: 20px;
        object-fit: cover;
        stroke: currentColor;
        fill: transparent;
      }

      &:hover {
        color: $accent;
      }
    }

    .swiper-button-next:after,
    .swiper-button-prev:after {
      display: none;
    }
  }
}

.cart-images__next {
  right: -9px;
}

.cart-images__prev {
  left: 0;
}