.catalog-menu {
  display: flex;
  flex-direction: column;
  margin-left: -60px;
  margin-bottom: 30px;

  &__item {
    &:not(:last-child) {
      margin-bottom: 1px;
    }
  }

  &__link {
    @include flex-v-center;
    padding: 12px 30px 12px 60px;
    background-color: $gray;
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    text-transform: uppercase;
    color: $default;
    transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;

    &--dropdown-active {
      color: $white;
      background-color: $accent;

      svg {
        transform: rotate(90deg);
      }
    }

    svg {
      display: block;
      flex-shrink: 0;
      margin-left: auto;
      width: 10px;
      height: 20px;
      stroke: currentColor;
      fill: transparent;
      transition:  transform .2s ease-in-out;
    }

    &:hover {
      color: $white;
      background-color: $accent;
    }

    &:active {
      color: $white;
      background-color: $accent;
    }
  }
}

.catalog-dropdown {
  // max-height: 0;
  // overflow: hidden;
  // will-change: max-height;
  // transition: opacity 0.2s ease-in-out, overflow 0.2s ease-in-out,
  //   max-height 0.2s ease-in-out, padding 0.2s ease-in-out;

  &--active {
    opacity: 1;
  }

  &__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 20px;
    color: rgba(34, 34, 34, 0.7);
    text-transform: none;

    &:hover {
      background-color: $gray;
      color: $accent;
    }
  }
}
