.top {
  background-color: #59636F;
}
.top-slider {
  &__item {
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center right;

    &::before {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(90deg, #59636F 30.73%, rgba(89, 99, 111, 0) 100%);
    }
    &::after {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url('../img/top-crop.png');
    }
  }
  &__item--meat {
    background-image: url('../img/top-1.jpg');
  }
  &__content {
    position: relative;
    z-index: 1;
    padding: 50px 0 50px;
    max-width: 480px;
    padding-left: 60px;
  }
  &__img {
    margin-bottom: 30px;
  }
  &__title {
    font-weight: 700;
    font-size: 48px;
    line-height: 60px;
    color: #e0e0e0;
    margin-bottom: 40px;
  }
  &__link {
    position: relative;
    display: inline-block;
    font-weight: 500;
    font-size: 24px;
    line-height: 33px;
    padding: 8px 71px 8px 40px;
    border: 1px solid $white;
    border-radius: 1px;
    color: $white;
    transition: color .5s ease-in-out;

    &::before {
      position: absolute;
      content: '';
      background-image: url('../img/link-arr.svg');
      top: 50%;
      right: 35px;
      transform: translateY(-50%);
      width: 20px;
      height: 18px;
    }

    &:focus {
      outline: 1px solid $accent;
      outline-offset: 2px;
    }
  
    &:hover {
      color: $gray;
    }
  
    &:active {
      color: $gray;
    }
  }
  &__pagination {
    display: flex;
    bottom: 30px!important;
    align-items: center;
    justify-content: center;
  }
  .swiper-pagination-bullet {
    width: 15px;
    height: 15px;
    margin: 0 12px;
    border-radius: 100%;
    border: 1px solid $accent;
    background-color: $white;
    opacity: 1;
  }
  .swiper-pagination-bullet-active {
    width: 20px;
    height: 20px;
    background-color: $accent;
    border-radius: 100%;
  }
}