.cart-product {
  padding-bottom: 50px;
  &__title {
    margin-bottom: 25px;
  }
  &__info {
    grid-column: 9 span;
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    gap: 30px;
  }

  &__descr {
    grid-column: 5 span;
  }

  &__text {
    font-size: 16px;
    line-height: 30px;
    color: $default;
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  &__aside {
    grid-column: 3 span;
  }

  &__tabs {
    grid-column: 7 span;
    width: 720px;
    margin-bottom: 50px;
  }

  &__back {
  }
}
