.cookie-block {
  position: fixed;
  display: none;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
  background-color: #c4c4c4;
  padding: 15px 0;
  &__container {
    display: flex;
    align-items: center;
    max-width: 1176px;
    padding: 0 15px;
    margin: 0 auto;
  }
  &__text {
    font-size: 16px;
    line-height: 10px;
    color: $default;
    margin-right: 40px;
  }
  &__btn {
    height: 24px;
    font-size: 16px;
    line-height: 22px;
    color: $white;
    padding: 1px 10px;
  }
}
