.catalog-page {
  padding-bottom: 50px;

  &__title {
    margin-bottom: 43px;
  }

  &__inner {
  }

  &__left {
    grid-column: 3 span;
  }
  &__right {
    grid-column: 9 span;
  }
  &__list {
    margin-bottom: 50px;
  }
  &__menu {
    display: none;
    margin-left: auto;
  }
}

.list-products {
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  gap: 30px;

  &__item {
    grid-column: 3 span;
    padding: 20px 30px;
    background-color: $white;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
    border-radius: 1px;
  }

  &__img {
    width: 190px;
    height: 170px;
    object-fit: contain;
    display: block;
    margin: 0 auto 16px;

  }

  &__title {
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    color: $default;
    margin-bottom: 16px;
    min-height: 38px;

    -webkit-line-clamp: 2 ;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    overflow: hidden;
  }

  &__link {
    min-height: 30px;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    color: $white;
  }
}