.catalog {
  padding: 60px 0 60px;
  background-color: $gray;
  &__title {
    margin-bottom: 35px;
    text-align: center;
  }
}



.catalog-list {
  &__item {
    grid-column: 4 span;
  }
}

.product {
  display: block;
  background-color: $white;
  padding: 30px 20px 34px;
  text-align: center;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
  border-radius: 3px;
  &__img {
    margin-bottom: 26px;
  }
  &__descr {
    font-size: 24px;
    line-height: 30px;
    min-height: 60px;
    transition: color .2s ease-in-out, text-decoration .2s ease-in-out;
  }
  .image {
    margin: 0 auto;
    object-fit: contain;
    height: 230px;
  }

  &:focus {
    outline: 1px solid $accent;
    outline-offset: 2px;
  }

  &:hover {
    .product__descr {
      color: $accent;
      text-decoration: underline;
    }
  }
}
