.aside-support {
  width: 307px;
  padding: 13px 26px 20px;
  background-color: $white;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  border-radius: 1px;

  &__title {
    margin-bottom: 17px;
    color: $default;
    font-weight: 700;
    font-size: 18px;
    line-height: 20px;
  }

  &__input {
    display: block;
    width: 100%;
    margin-bottom: 20px;
  }

  &__btn {
    display: block;
    min-height: 30px;
    margin: 0 auto;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
  }
}
