.to-top {
  @include flex-all-center;
  width: 30px;
  height: 30px;
  background-color: $default;
  border-radius: 100%;
  color: $default;
  transition: background-color .3s ease-in-out, color .3s ease-in-out;
  svg {
    width: 18px;
    height: 10px;
    stroke: $white;
    fill: currentColor;
  }

  &:focus {
    outline: 1px solid $accent;
    outline-offset: 2px;
  }

  &:hover {
    background-color: $accent-hover;
    color: $accent-hover;
  }

  &:active {
    background-color: $accent-hover;
  }
}
