.contact {
  padding-bottom: 50px;

  &__title {
    margin-bottom: 26px;
  }

  &__info {
    grid-column: 5 span;
  }

  &__info-list {
    margin-bottom: 25px;
  }

  &__info-item {
    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  &__info-address {
    font-style: normal;

    span {
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
    }
  }

  &__info-text {
    font-weight: 700;
    font-size: 18px;
    line-height: 25px;
    color: $default;
  }

  &__link {
    transition: color 0.2s ease-in-out;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;

    &:focus {
      outline: 1px solid $accent;
      outline-offset: 2px;
    }

    &:hover {
      color: $accent;
    }

    &:active {
      color: $accent;
    }
  }

  &__form {
    margin-bottom: 50px;
  }


  &__map {
    grid-column: 7 span;
    height: 350px;
  }
}


[class*="copyrights-pane"] {
  display: none !important;
}

.balloon {
  text-align: center;

  &__title {
    font-size: 15px;
    line-height: 18px;
    margin-bottom: 5px;
    color: $default;
  }

  &__link {
    color: $text-color;
    transition: color .2s ease-in-out;

    &:hover {
      color: $accent;
    }
  }
}