@media (max-width: 1400px) {
  .delivery__inner {
    flex-direction: column;
  }

  .delivery__content {
    max-width: 100%;
    margin-right: 0;
    margin-bottom: 30px;
  }

  .russian-map {
    text-align: center;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  .delivery-content__text {
    margin-bottom: 10px;
  }

  .about-page__content {
    grid-column: 8 span;
  }
}

@media (max-width: 1300px) {
  .cart-product__info {
    grid-column: 12 span;
  }

  .cart-images {
    width: 100%;
  }

  .cart-product__aside {
    grid-column: 12 span;
    order: 1;
    display: flex;
    align-items: flex-start;
  }

  .cart-product__tabs {
    grid-column: 12 span;
    margin-bottom: 0;
  }

  .aside__info {
    margin-right: 20px;
  }

  .catalog-page {
    .aside {
      display: none;
    }
  }
}

@media (max-width: 1200px) {
  .home-form {
    padding: 0;
  }

  .home-form::before {
    display: none;
  }

  .blog-post__title {
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 20px;
  }

  .about__text:nth-child(2n+2) {
    margin-bottom: 18px;
  }

  .footer__inner {
    padding: 20px 15px;
    justify-content: space-between;
  }

  .footer__nav .nav__item:not(:last-child) {
    margin-right: 10px;
  }

  .footer {
    .header__content-link:not(:last-child) {
      margin-right: 0;
    }
  }

  .footer__tel {
    font-size: 16px;
    line-height: 20px;
  }

  .catalog-page__title {
    margin-bottom: 25px;
  }

  .catalog-menu {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    justify-content: center;
    width: 350px;
    min-height: 100vh;
    // height: 100%;
    z-index: 100;
    padding-left: 10px;
    overflow-y: auto;
    transform: translateX(-100%);

    transition: transform .3s ease-in-out;

    &--active {
      transform: translateX(0%);
    }
  }

  .catalog-page__menu {
    display: block;
  }

  .catalog-page__right {
    grid-column: 12 span;
  }

  .catalog-page__left {
    grid-column: 12 span;
  }
}

@media (max-width: 992px) {
  .header {
    &__inner {
      padding-top: 0;
      margin-bottom: 15px;
    }

    .nav__item:not(:last-child) {
      margin-right: 10px;
    }

    .nav__link {
      font-size: 14px;
    }

    .nav__link-arr {
      padding-left: 0;
    }

    .btn-fill {
      padding: 7px 10px;
    }
  }

  .top-slider__pagination {
    bottom: 10px !important;
  }

  .benefits-list__item {
    grid-column: 6 span;
    padding-top: 86px;
  }

  .catalog-list__item {
    grid-column: 6 span;
  }

  .product__descr {
    font-size: 20px;
    line-height: 25px;
  }

  .grid__item {
    grid-column: 6 span;
  }

  .footer__tel {
    display: none;
  }

  .footer__btn-call {
    margin-right: 10px;
  }

  .footer__nav {
    margin-left: auto;
    margin-right: auto;
  }

  .about-page__container {
    column-gap: 0;
  }

  .about-page__content {
    grid-column: 12 span;
    margin-bottom: 30px;
  }

  .about-content__form {
    max-width: 100%;
  }

  .form {
    max-width: 100%;
  }

  .about-page__aside {
    grid-column: 12 span;
    display: flex;
  }

  .aside__catalog {
    margin-right: 20px;
  }

  .form__label {
    width: 100%;
  }

  .form__input {
    width: 100%;
  }

  .contact__info,
  .contact__map {
    grid-column: 12 span;
  }

  .list-products {
    grid-template-columns: repeat(12, 1fr);
  }

  .list-products__item {
    grid-column: 6 span;
  }
}

@media (max-width: 768px) {
  .header {
    .nav {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      height: 100vh;
      z-index: 100;
      transform: translateY(-150%);
      transition: transform 0.3s ease-in-out;
    }

    .nav__list {
      align-items: center;
      justify-content: center;
      height: 100%;
      flex-direction: column;
      background-color: rgba($color: $default, $alpha: .9);
    }

    .nav__item {
      margin-bottom: 30px;
    }

    .nav__item:not(:last-child) {
      margin-right: 0;
    }

    .nav__link {
      color: $gray;
      font-size: 22px;
    }

    .menu--active {
      transform: translateY(0%);

      &~.burger {
        color: $gray;
      }
    }
  }

  .menu-dropdown {
    width: 200px;
  }

  .burger {
    display: block;
    z-index: 200;
    order: 1;
  }

  .top-slider__item::after {
    display: none;
  }

  .top-slider__item {
    background-position: center;
  }

  .catalog-list__item {
    grid-column: 12 span;
  }

  .home-form__title {
    font-size: 32px;
    line-height: 40px;
  }

  .home-form__text {
    max-width: 100%;
    margin-bottom: 15px;
    font-size: 16px;
    line-height: 24px;
  }

  .home-form__form-box {
    flex-direction: column;
  }

  .home-form__form-input {
    max-width: 100%;
    margin-right: 0px;
    width: 100%;
  }

  .footer__nav {
    display: none;
  }

  .footer__inner {
    justify-content: start;
  }

  .footer__btn-call {
    margin: 0 20px;
  }

  .cart-product__tabs {
    width: 100%;
  }

  .cart-images,
  .cart-product__descr {
    grid-column: 12 span;
  }

  .tabs__nav-btn {
    min-height: 58px;
  }
}

@media (max-width: 576px) {
  .top-slider__content {
    padding: 25px 0 40px 0px;
    max-width: 100%;
  }

  .top-slider__img {
    margin-bottom: 15px;
    height: 100px;
  }

  .top-slider__title {
    font-size: 30px;
    line-height: 40px;
    margin-bottom: 15px;
  }

  .top-slider__link {
    font-size: 16px;
    line-height: 1;
    padding: 5px 8px 8px;
  }

  .top-slider__item::before {
    width: 60%;
  }

  .top-slider__link::before {
    display: none;
  }

  .top-slider .swiper-pagination-bullet {
    width: 8px;
    height: 8px;
  }

  .benefits,
  .catalog,
  .home-form__content,
  .news,
  .about,
  .delivery {
    padding: 40px 0;
  }

  .benefits-list__item {
    grid-column: 12 span;
    padding-top: 53px;
    background-size: 50px;
  }

  .benefits-list__title {
    font-size: 26px;
    line-height: 32px;
    margin-bottom: 10px;
  }

  .benefits-list__txt {
    font-size: 16px;
    line-height: 20px;
    padding-bottom: 10px;
    margin-bottom: 8px;

    &::before {
      height: 2px;
    }
  }

  // title-bottom
  .benefits__title,
  .catalog__title,
  .news__title,
  .about__title,
  .delivery-content__title,
  .title {
    margin-bottom: 25px;
  }

  .title {
    font-size: 35px;
    line-height: 40px;

    &::before {
      top: -10px;
      height: 2px;
    }
  }

  .grid {
    gap: 0;
    row-gap: 20px;
  }

  .product {
    padding: 20px;
  }

  .product__img {
    margin-bottom: 15px;
  }

  .product .image {
    height: 150px;
  }

  .home-form__title {
    font-size: 26px;
    line-height: 32px;
  }

  .news-list {
    grid-template-rows: 250px;
  }

  .blog-post__title {
    font-size: 18px;
    line-height: 22px;
  }

  .grid__item {
    grid-column: 12 span;
  }

  .blog-post__link {
    height: 300px;
  }

  .russian-map__tool {
    padding: 3px 5px;
    font-size: 8px;
    line-height: 9px;
  }

  .form-searh__input--active {
    width: 300px;
    outline: 0;
  }

  .title-page {
    font-size: 32px;
    line-height: 40px;
  }

  .about-page {
    padding-bottom: 40px;
  }

  .about-content__form {
    margin-bottom: 25px;
  }

  .about-content__title {
    margin-bottom: 10px;
  }

  .about-content__info {
    margin-bottom: 15px;
  }

  .about-content__form-title {
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 20px;
  }

  .about-page__aside {
    flex-direction: column;
  }

  .cart-product__info {
    gap: 0;
    row-gap: 20px;
  }

  .cart-images__slider .image {
    max-width: 320px;
    max-height: 200px;
  }

  .cart-product__text:not(:last-child) {
    margin-bottom: 15px;
  }

  .tabs__content {
    padding: 15px;
  }

  .tabs__nav-btn {
    padding: 5px 5px;
    font-size: 14px;
    line-height: 1;
  }

  .tabs__download-descr,
  .tabs__download-link {
    font-size: 14px;
    line-height: 18px;
  }

  .tabs__download-descr {
    padding-left: 15px;

    svg {
      display: none;
    }
  }

  .tabs__content {
    overflow-x: scroll;
  }

  .aside__catalog {
    margin-bottom: 25px;
    margin-right: 0;
  }

  .aside-catalog,
  .aside-info,
  .aside-support {
    width: 100%;
    padding: 16px;
  }

  .aside-catalog__title,
  .aside-info__title {
    font-size: 22px;
    line-height: 30px;
    margin-bottom: 15px;
  }

  .contact__form {
    margin-bottom: 25px;
  }

  .cart-product__aside {
    flex-direction: column;
    margin-bottom: 25px;
  }

  .aside__info {
    margin-right: 0;
  }

  .list-products {
    gap: 0;
    row-gap: 20px;
  }

  .list-products__item {
    grid-column: 12 span;
    padding: 15px 20px;
  }

  .list-products__img {
    width: 160px;
    height: 120px;
  }

  .catalog-menu__item:not(:last-child) {
    margin-bottom: 0px;
  }

  .catalog-menu__link {
    padding: 10px 10px 10px 60px;
    font-size: 12px;
    line-height: 18px;
  }

  .catalog-menu {
    width: 230px;
  }

  .catalog-page__menu {
    padding: 7px 15px;
    font-size: 12px;
    min-height: 40px;
    line-height: 18px;
  }
}