.header {
  position: relative;
  padding: 15px 0;
  background-color: $white;

  &--page {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
  }

  &__inner {
    @include flex-all-center;
    justify-content: space-between;
    padding-top: 20px;
  }

  &__content-top {
    @include flex-v-center;
    padding-bottom: 16px;
    margin-bottom: 13px;
    border-bottom: 1px solid $text-color;
  }

  &__content-tel {
    padding-right: 124px;

    &::before {
      position: absolute;
      content: "(  )";
      bottom: 0;
      right: 0;
      font-weight: 300;
      font-size: 10px;
      line-height: 16px;
      color: currentColor;
    }

    svg {
      width: 18px;
      height: 14px;
      stroke: currentColor;
      fill: currentColor;
    }
  }

  &__content-link {
    position: relative;
    display: block;
    font-size: 14px;
    line-height: 19px;
    color: $default;
    transition: color 0.2s ease-in-out;

    &:not(:last-child) {
      margin-right: 45px;
    }

    &:focus {
      outline: 1px solid $accent;
      outline-offset: 2px;
    }

    &:hover {
      color: $accent;
    }

    &:active {
      color: $accent;
    }

    svg {
      vertical-align: middle;
    }
  }

  &__content-ico {
    padding-right: 10px;
  }

  &__content-email {
    svg {
      width: 18px;
      height: 14px;
      stroke: currentColor;
      fill: transparent;
    }
  }

  &__content-loca {
    svg {
      width: 12px;
      height: 14px;
      fill: currentColor;
    }
  }

  &__content-inst {
    svg {
      width: 16px;
      height: 16px;
      stroke: currentColor;
      fill: transparent;
    }
  }

  &__right {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
}

.btn-call {
  // margin-bottom: 23px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
}

.burger {
  display: none;
  @include burder;
}