.about {
  padding: 55px 0 55px;
  &__title {
    margin-bottom: 28px;
  }
  &__content {
    column-count: 2;
    column-gap: 30px;
    column-width: 645px;
  }
  &__text {
    font-size: 18px;
    line-height: 20px;
    margin-bottom: 18px;
    span {
      font-weight: 700;
      font-size: 18px;
      line-height: 20px;
      color: $default;
    }
    &:nth-child(2n + 2) {
      margin-bottom: 0;
    }
  }
}
