.graph-modal {
  &__container {
    background-color: $white;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
    border-radius: 1px;
    width: 420px;
    height: 250px;
    padding: 13px 50px 20px;

  }

  &__close {
    @include flex-all-center;
    margin-bottom: 11px;
    width: 12px;
    height: 12px;
    color: $default;
    transition: color .2s ease-in-out;

    svg {
      stroke: currentColor;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &:focus {
      outline: 1px solid $accent;
      outline-offset: 2px;
    }
  
    &:hover {
      color: $accent;
    }
  
    &:active {
      color: $accent;
    }
  }

}

.modal-content {
  text-align: center;

  &__title {
    font-weight: 700;
    font-size: 18px;
    line-height: 20px;
    color: $accent;
    margin-bottom: 6px;
  }

  &__text {
    font-size: 16px;
    line-height: 30px;
    color: $default;
    margin-bottom: 11px;
  }

  &__form {
    margin-bottom: 11px;
  }

  &__input {
    display: inline-block;
    margin-bottom: 20px;
  }

  &__btn {
    min-height: 30px;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    padding: 7px 20px;
  }

  &__descr {
    color: rgba(34, 34, 34, 0.5);
    font-size: 10px;
    line-height: 10px;
    text-align: center;
  }

  &__descr-link {

    color: $accent;
    transition: color .2s ease-in-out;

    &:focus {
      outline: 1px solid $accent;
      outline-offset: 1px;
    }

    &:hover {
      color: $accent-hover;
    }

    &:active {
      color: $accent-hover;
    }
  }
}